import React from "react";
import styled from "styled-components";
import { ErrorMessage } from "../helpers/form-sections";

export const LabeledFileInput = ({
  placeholder,
  label,
  id,
  name,
  required,
  value,
  icon = null,
  isInvalid,
  ...props
}) => (
  <InputWrapper>
    <StyledLabel htmlFor={id}>
      {label} {!required && <OptionalSpan> - Optional</OptionalSpan>}
      <IconFileInput $placeholder={placeholder} $isInvalid={isInvalid}>
        <StyledFileInput type="file" id={id} name={name} {...props} />
        {icon && <IconWrapper>{icon}</IconWrapper>}
        <FileInfo $hasFile={!!value}>
          {value ? value.name : "Upload CV"}
        </FileInfo>
      </IconFileInput>
    </StyledLabel>
    <ErrorMessage>{isInvalid && <span>{label} is required</span>}</ErrorMessage>
  </InputWrapper>
);

const InputWrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 100%;
`;

const StyledFileInput = styled.input`
  display: none;
`;

const StyledLabel = styled.label`
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.2px;
  color: #47474f;
`;

const OptionalSpan = styled.span`
  color: #cacfe0;
`;

const IconWrapper = styled.div`
  position: absolute;
  top: 14px;
  right: 19px;
`;

const IconFileInput = styled.div`
  position: relative;
  cursor: pointer;
  background: #ffffff;
  border: 2px solid ${({ $isInvalid }) => ($isInvalid ? "#ed5252" : "#eeedf3")};
  border-radius: 5px;
  padding: 16px;
  height: 56px;
  box-sizing: border-box;
  width: 100%;
  margin-top: 8px;

  &:active,
  &:focus {
    color: #060831;
    outline: none;
    border: 2px solid #4200ff;
  }

  &:disabled {
    background: #0000001a;
    opacity: 0.5;
  }
`;

const FileInfo = styled.span`
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.2px;
  position: absolute;
  color: ${({ $hasFile }) => ($hasFile ? "#0f1328" : "#757ba9")};
  top: 18px;
  left: 14px;
`;
