import React, { useState } from "react";
import styled from "styled-components";
import { SubmitButton } from "../components/ButtonLink";
import { Spinner } from "./form-sections";
import { media } from "./media-queries";
import { LabeledInput } from "../components/LabeledInput";
import { LabeledFileInput } from "../components/LabeledFileInput";
import { LabeledTextarea } from "../components/LabeledTextarea";
import { FormElementsColumnWrapper } from "../helpers/form-sections";
import Attachment from "../images/attachment.svg";
import InputValidIcon from "../images/input_valid.svg";
import { isEmailFormatValid } from "../helpers/email";

export const CareersForm = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [file, setFile] = useState("");
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [loading, setLoading] = React.useState(false);

  const isFormSatisfied = name && isEmailFormatValid(email) && file && message;

  const onFormSubmit = async (e) => {
    e.preventDefault();

    setIsSubmitted(true);

    if (!isFormSatisfied) {
      return;
    }

    setLoading(true);

    const url =
      "https://europe-west1-vosbor-io.cloudfunctions.net/function-vosbor-form-mailer";
    const formData = new FormData();
    formData.append("type", "careers");
    formData.append("name", name);
    formData.append("email", email);
    formData.append("message", message);
    formData.append("file", file);
    const config = {
      method: "POST",
      mode: "cors",
      cache: "no-cache",
      body: formData,
    };

    try {
      const response = await fetch(url, config);

      console.log(response.data);
      window.location = "/response-ok";
    } catch (error) {
      console.log("ERROR:", error);
      setLoading(false);
    }
  };

  return (
    <FormWrapper>
      {loading && <Spinner />}
      <Form
        action="https://europe-west1-vosbor-io.cloudfunctions.net/function-vosbor-form-mailer"
        method="POST"
        autoComplete="off"
        enctype="multipart/form-data"
        onSubmit={onFormSubmit}
      >
        <input type="hidden" name="type" value="career" />
        <FormElementsColumnWrapper>
          <LabeledInput
            type="text"
            name="name"
            placeholder="Enter full name"
            label="Full name"
            id="name"
            autoComplete="off"
            onChange={(e) => setName(e?.target?.value)}
            disabled={loading}
            required
            icon={name && <InputValidIcon />}
            isInvalid={isSubmitted && name === ""}
          />
          <LabeledInput
            type="email"
            placeholder="Enter email address"
            label="Email address"
            name="email"
            id="email"
            autoComplete="off"
            onChange={(e) => setEmail(e?.target?.value)}
            disabled={loading}
            required
            icon={isEmailFormatValid(email) && <InputValidIcon />}
            isInvalid={isSubmitted && !isEmailFormatValid(email)}
          />
        </FormElementsColumnWrapper>
        <LabeledFileInput
          name="file"
          placeholder="Upload CV"
          label="Attachment"
          id="filecv"
          autoComplete="off"
          onChange={(ev) => setFile(ev.target.files[0])}
          disabled={loading}
          icon={file ? <InputValidIcon /> : <Attachment />}
          value={file}
          isInvalid={isSubmitted && !file}
          required
        />
        <LabeledTextarea
          placeholder="I am interested in joining Vosbor because..."
          name="message"
          autoComplete="off"
          onChange={(e) => setMessage(e?.target?.value)}
          disabled={loading}
          label="Message"
          icon={message && <InputValidIcon />}
          isInvalid={isSubmitted && message === ""}
          required
        />
        <InfoText>
          Your data will only be used by authorised Vosbor personnel, not
          distributed to third parties and only used to contact you and to send
          you our newsletter. By using the contact form you agree to our privacy
          policy.
        </InfoText>
        <StyledSubmitButton disabled={loading}>Submit</StyledSubmitButton>
      </Form>
    </FormWrapper>
  );
};

const FormWrapper = styled.div`
  ${media.m`
    display: flex;
    flex-direction: column;
    align-items: center;
  `}
`;

const Form = styled.form`
  display: flex;
  flex-direction: column;
  align-items: stretch;

  ${media.m`
    max-width: 560px;
    width: 100%;
  `}
`;

const InfoText = styled.span`
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 9px;
  line-height: 18px;
  text-align: right;
  letter-spacing: 0.002em;
  color: #747487;
  ${media.m`
    max-width: 440px;
    align-self: end;
  `}
`;

const StyledSubmitButton = styled(SubmitButton)`
  margin-top: 24px;
  width: 100%;

  ${media.m`
    width: 160px;
  `}
`;
